import classes from './MobileDropDown.module.css'
import { Accordion } from 'react-bootstrap';
import {useEffect, useState} from 'react';
import { useLocation } from 'react-router-dom';
import {NavLink} from 'react-router-dom'

const MobileDropDown = ({ cycleBrand, accessBrand, categories, sidebar, changeSidebar, changeLocation}) => {



    let riderOption = [
        { label: 'Backpacks', value: 'Backpacks' },
        { label: 'Compression and Inner Wear', value: 'Compression and Inner Wear' },
        { label: 'Eyewear', value: 'Eyewear' },
        {label: 'Face Masks', value: 'Face Masks'},
        { label: 'Footwear', value: 'Footwear' },
        { label: 'Gloves', value: 'Gloves' },
        { label: 'Helmets', value: 'Helmets' },
        { label: 'Jerseys', value: 'Jerseys' },
        { label: 'Recovery and Body Care', value: 'Recovery and Body Care' },
        { label: 'Shorts', value: 'Shorts' },
        {label: 'T-Shirts', value: 'T-Shirts'}
    ]
    let cycleOptions = [
        { label: 'Bags and Car Racks', value: 'Bags and Car Racks' },
        { label: 'Bells and Horns', value: 'Bells and Horns' },
        { label: 'Bottles and Bottle Cages', value: 'Bottles and Bottle Cages' },
        { label: 'Components and Spares', value: 'Components and Spares' },
        { label: 'GPS and Cyclocomputers', value: 'GPS and Cyclocomputers' },
        { label: "Lights", value: 'Lights' },
        { label: "Locks", value: "Locks" },
        { label: "Maintenance and Care", value: 'Maintenance and Care' },
        { label: "Mudguards and Protection", value: "Mudguards and Protection" },
        { label: 'Others', value: 'Other' },
        { label: 'Pumps', value: 'Pumps' },
        { label: 'Stands', value: 'Stands' },
        { label: 'Tires and Tubes', value: 'Tires and Tubes' },
        { label: 'Tools', value: 'Tools' },
        { label: 'Trainers', value: 'Trainers' },
        { label: 'Wheels', value: 'Wheels' },
        { label: 'Wheels', value: 'Wheels' }
    ]

    const locationHandler = () => {
        changeLocation((prevState)=>{
            return prevState + 1
        })
    }


    const dataArray = [{name: 'Male'}, {name: 'Female'}, {name: 'Young Male'}, {name: 'Young Female'}]
    return (
        <div className={classes.mobileBackground}>
            <div className={classes.spanBackground}>
                <span>{'Menu'}</span>
                <span onClick={()=>{
                    changeSidebar(false)
                }} className={classes.iconBtn}><i class="fas fa-times"></i></span>
            </div>     
            <div className={classes.pDiv}>
                <p>Cart</p>
                <p>Checkout</p>
               <div className={classes.categoryDiv}>
                <Accordion>
                <Accordion.Item eventKey="0">
                    <Accordion.Header>Bicycles</Accordion.Header>
                    <Accordion.Body>
                          
                    <Accordion style={{margin: '10px 0px'}}>
                        <Accordion.Item eventKey="0">
                            <Accordion.Header>Users</Accordion.Header>
                            <Accordion.Body>
                                {dataArray.map((singleItem)=>{
                                    return (
                                        <p><NavLink onClick={locationHandler} to={`/productDisplay/cycles/users/${singleItem.name}`}>{singleItem.name}</NavLink></p>
                                    )
                                })}
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="1">
                            <Accordion.Header>Category</Accordion.Header>
                            <Accordion.Body>    
                                {categories.map((singleItem)=>{
                                    return(
                                        <p><NavLink onClick={locationHandler} to={`/productDisplay/cycles/category/${singleItem.name}`}>{singleItem.name}</NavLink></p>
                                    )
                                })}
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="2">
                            <Accordion.Header>Brand</Accordion.Header>
                            <Accordion.Body>
                                {cycleBrand.Cycle && cycleBrand.Cycle.map((singleItem)=>{
                                    return (
                                        <p><NavLink onClick={locationHandler} to={`/productDisplay/cycles/brand/${singleItem}`}>{singleItem}</NavLink></p>
                                    )
                                })}
                            </Accordion.Body>
                        </Accordion.Item>
                        </Accordion>
                   </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="1">
                    <Accordion.Header>Accessories</Accordion.Header>
                    <Accordion.Body>
                                                    
                <Accordion style={{margin: '10px 0px'}}>
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>For the Rider</Accordion.Header>
                        <Accordion.Body>
                                {riderOption.map((singleItem)=>{
                                    return <p><NavLink to={`/productDisplay/accessories/riderType/${singleItem.label}`}>{singleItem.label}</NavLink></p>
                                })}
                        </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="1">
                        <Accordion.Header>For the Cycle</Accordion.Header>
                        <Accordion.Body>
                                {cycleOptions.map((singleItem)=>{
                                    return <p><NavLink to={`/productDisplay/accessories/cycleType/${singleItem.label}`}>{singleItem.label}</NavLink></p>
                                })}
                        </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="2">
                        <Accordion.Header>Brand Name</Accordion.Header>
                        <Accordion.Body>
                                {cycleBrand.access && cycleBrand.access.map((singleItem)=>{
                                    return <p><NavLink to={`/productDisplay/accessories/brand/${singleItem}`}>{singleItem}</NavLink></p>
                                })}
                        </Accordion.Body>
                    </Accordion.Item>
                    </Accordion>
                    </Accordion.Body>
                </Accordion.Item>
                </Accordion>
            </div>
        </div>     
    </div>
        
    )
}

export default MobileDropDown